export default async function ProductPage ({ categoryId }) {
    console.log("Received Category ID:", categoryId); // Debugging

    if (!categoryId) {
        return <div>Error: No category ID provided</div>; // ✅ Return JSX
    }

    const wixClient = await wixClientServer();

    try {
        const res = await wixClient.products
            .queryProducts()
            .hasSome("collectionIds", [categoryId]) // Ensure it's an array
            .find();

        console.log("Query Result:", res); // Debugging

        if (!res.items || res.items.length === 0) {
            return <div>No products found for this category.</div>; // ✅ Return JSX
        }

        return (
            <div>
                <h2>Products</h2>
                <ul>
                    {res.items.map((product) => (
                        <li key={product._id}>{product.name}</li>
                    ))}
                </ul>
            </div>
        );
    } catch (error) {
        console.error("Wix API Error:", error);
        return <div>Error: {error.message || "Failed to fetch products"}</div>; // ✅ Return JSX
    }
};
